.wrap {
  height: 500px;
  padding: 0 160px;
  background-color: #fff;
  .top {
    display: flex;
    justify-content: space-between;
    .dynamic {
      width: 300px;
      height: 300px;
    }
    .info {
      margin-top: 20px;
      .title {
        color: #0d2f3f;
        font-size: 25px;
        font-weight: bold;
        margin-bottom: 16px;
      }
      .list {
        font-size: 25px;
        .item {
          &:hover {
            .icon {
              animation: jello 0.5s;
            }
            .link {
              text-decoration: underline;
              cursor: pointer;
            }
          }

          .link {
            font-size: 16px;
            margin-left: 10px;
            color: #286f6c;
          }
          .desc {
            margin-left: 10px;
            font-size: 16px;
            color: #286f6c;
          }
        }
      }
    }
  }
  .bottom {
    &::before {
      content: '';
      display: block;
      width: 100%;
      height: 3px;
      background-color: #dddddd;
      margin-top: 20px;
    }
    .tech {
      margin-top: 10px;
      color: #0d2f3f;
      font-size: 25px;
      text-align: right;
      .text {
        font-size: 16px;
      }
    }
  }
}

@keyframes jello {
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(0.9, 1.1);
  }
  50% {
    transform: scale(1.2, 0.8);
  }
  75% {
    transform: scale(0.95, 1.05);
  }
}
